body {
  margin: 0;
  padding: 0;
  font: 20px/1.4 sans-serif;
  word-break: keep-all;
  word-wrap: break-word;
}

* { margin: 0;
      padding: 0; 
}

img { vertical-align: top; max-width: 100%; border: 0 none; border-radius: 10px; }

.loading { 
  font-size: 6vw;
  line-height: 1.6;
  font-weight: 700;
  text-align: center;
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 30vh 0;
}
.App { display: -ms-flexbox; display: flex; -ms-flex-align: center; align-items: center; -ms-flex-pack: center; justify-content: center; -ms-flex-wrap: wrap; flex-wrap: wrap; padding: 1%; margin-top: -2%; margin-left: -2%; }
.App:after { display: block; content: ''; clear: both; }
.movie { position: relative; 
              width: 500px; 
              padding: 1% 1% 1% 170px; 
              -webkit-box-sizing: border-box; 
                      box-sizing: border-box; 
              min-height: 225px; 
              margin: 2% 0 0 2%; 
              -webkit-box-shadow: 1px 1px 5px 5px rgba(0, 0, 0, 0.5); 
                      box-shadow: 1px 1px 5px 5px rgba(0, 0, 0, 0.5);
              border-radius: 10px; 
              background-color: #fff;  
              -webkit-transition: all 0.5s;  
              -o-transition: all 0.5s;  
              transition: all 0.5s; 
            }
.movie:hover { -webkit-box-shadow: 1px 1px 5px 5px #000; box-shadow: 1px 1px 5px 5px #000; 
                      -webkit-transform: scale(1.05); 
                          -ms-transform: scale(1.05); 
                              transform: scale(1.05); 
                      z-index: 10; 
                    }
.LinesEllipsis { overflow: hidden; }
.movie h1 { margin-bottom: 10px;  font-size: 16px; }
.movie .thum { text-align: center; width: 150px;  position: absolute; left: 0; top: 0; }
.movie .thum img {border-radius:  10px 0 0 10px;  }
.movie_genre { margin-bottom: 20px; }
.movie_genre span { font-size: 14px; font-weight: bold; }
.movie_genre span:after { content: ', '; }
.movie_genre span:last-child:after { content: none; }

.movie_synopsis { font-size: 13px; }

@media all and (max-width: 500px){
  .App { padding: 20px; }
  .movie { min-height: 0; padding: 10px; margin-bottom: 20px; }
  .movie:hover { -webkit-transform: none; -ms-transform: none; transform: none; }
  .movie .thum { position: static; max-width: 300px; margin-bottom: 10px; }
  .movie .thum img {border-radius: 10px; }
  .movie_genre { margin-bottom: 10px; }
  
}



